import { render, screen, waitFor, fireEvent } from '@testing-library/react';
import { jest, describe, it, expect, beforeEach } from '@jest/globals';

// Mock Next.js router - MUST be before component import
const mockPush = jest.fn();
jest.mock('next/navigation', () => ({
  useRouter: () => ({
    push: mockPush,
  }),
}));

import { ActivityPreviewModal } from './ActivityPreviewModal';

// Mock fetch
global.fetch = jest.fn() as jest.MockedFunction<typeof fetch>;

describe('ActivityPreviewModal', () => {
  const mockActivity = {
    id: 'activity-123',
    title: 'Beach Volleyball',
    date: '2024-06-14T10:00:00.000Z',
    time: '10:00 AM',
    location: 'Main Beach',
    description: 'Fun beach games for all ages',
    capacity: 20,
    capacityRemaining: 8,
    cost: 50,
    hostSubsidy: 20,
    slug: 'beach-volleyball',
  };

  const mockRsvpStatus = {
    status: 'attending' as const,
    guestCount: 2,
    dietaryRestrictions: 'Vegetarian',
  };

  beforeEach(() => {
    jest.clearAllMocks();
    (global.fetch as jest.MockedFunction<typeof fetch>).mockClear();
  });

  it('should render modal with activity data', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [mockRsvpStatus] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Beach Volleyball')).toBeInTheDocument();
    });

    expect(screen.getByText('10:00 AM')).toBeInTheDocument();
    expect(screen.getByText('Main Beach')).toBeInTheDocument();
    expect(screen.getByText('Fun beach games for all ages')).toBeInTheDocument();
  });

  it('should display capacity information', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('8 / 20 spots')).toBeInTheDocument();
    });

    expect(screen.getByText(/Available/)).toBeInTheDocument();
  });

  it('should display cost information with subsidy', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('$30.00')).toBeInTheDocument(); // 50 - 20 = 30
    });

    expect(screen.getByText(/\$50\.00 - \$20\.00 host subsidy/)).toBeInTheDocument();
  });

  it('should display RSVP status and dietary restrictions', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [mockRsvpStatus] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Attending')).toBeInTheDocument();
    });

    expect(screen.getByText('Guest count: 2')).toBeInTheDocument();
    expect(screen.getByText('Your Dietary Restrictions')).toBeInTheDocument();
    expect(screen.getByText('Vegetarian')).toBeInTheDocument();
  });

  it('should show capacity status as "Almost Full" when 90% full', async () => {
    const almostFullActivity = {
      ...mockActivity,
      capacity: 100,
      capacityRemaining: 5, // 95% full
    };

    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: almostFullActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText(/Almost Full/)).toBeInTheDocument();
    });
  });

  it('should show capacity status as "Full" when no spots remaining', async () => {
    const fullActivity = {
      ...mockActivity,
      capacity: 20,
      capacityRemaining: 0,
    };

    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: fullActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText(/Full/)).toBeInTheDocument();
    });
  });

  it('should show RSVP Now button when no RSVP exists', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('RSVP Now')).toBeInTheDocument();
    });
  });

  it('should navigate to full details page when View Full Details clicked', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('View Full Details')).toBeInTheDocument();
    });

    fireEvent.click(screen.getByText('View Full Details'));

    expect(mockPush).toHaveBeenCalledWith('/activity/beach-volleyball');
    expect(onClose).toHaveBeenCalled();
  });

  it('should navigate to RSVP page when RSVP Now clicked', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('RSVP Now')).toBeInTheDocument();
    });

    fireEvent.click(screen.getByText('RSVP Now'));

    expect(mockPush).toHaveBeenCalledWith('/guest/rsvp?activityId=activity-123');
    expect(onClose).toHaveBeenCalled();
  });

  it('should close modal when close button clicked', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Beach Volleyball')).toBeInTheDocument();
    });

    const closeButton = screen.getByLabelText('Close');
    fireEvent.click(closeButton);

    expect(onClose).toHaveBeenCalled();
  });

  it('should display loading state while fetching data', () => {
    (global.fetch as jest.MockedFunction<typeof fetch>).mockImplementation(
      () => new Promise(() => {}) // Never resolves
    );

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    // Check for loading spinner
    const loadingSpinner = document.querySelector('.animate-spin');
    expect(loadingSpinner).toBeInTheDocument();
  });

  it('should display error message when fetch fails', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>).mockResolvedValueOnce({
      ok: false,
      json: async () => ({ success: false, error: { message: 'Failed to load activity' } }),
    } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Failed to load activity')).toBeInTheDocument();
    });
  });

  it('should handle activity without optional fields', async () => {
    const minimalActivity = {
      id: 'activity-123',
      title: 'Simple Activity',
      date: '2024-06-14T10:00:00.000Z',
    };

    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: minimalActivity }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<ActivityPreviewModal activityId="activity-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Simple Activity')).toBeInTheDocument();
    });

    // Should not display optional fields
    expect(screen.queryByText('Time')).not.toBeInTheDocument();
    expect(screen.queryByText('Location')).not.toBeInTheDocument();
    expect(screen.queryByText('Description')).not.toBeInTheDocument();
    expect(screen.queryByText('Capacity')).not.toBeInTheDocument();
    expect(screen.queryByText('Cost per Guest')).not.toBeInTheDocument();
  });
});
